<template>
    <div>
    <basic-header :items="['店铺管理','分类管理']"></basic-header>
    <el-card>
        <el-tabs v-model="activeName">
            <!--一-->
            <el-tab-pane label="分类管理" name="first">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column label="id"  prop="id" fixed="left"></el-table-column>
                    <el-table-column label="用户名" prop="name"></el-table-column>
                    <el-table-column label="图片"  prop="thumb">
                        <template #default="o">
                            <img :src="o.row.thumb" alt="" width="100px" height="100px">
                        </template>
                    </el-table-column>
                    <el-table-column label="排序"  prop="sort"></el-table-column>
                    <el-table-column label="创建时间" prop="create_time"></el-table-column>
                    <el-table-column label="修改时间" prop="update_time"></el-table-column>
                    <el-table-column fixed="right" label="操作">
                        <template #default="o">
                            <el-button type="text" size="small" @click="edit(o.row)">编辑</el-button>
                            <el-button type="text" size="small" @click="del(o.row.id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!--分页-->
                <el-pagination
                        background
                        layout="prev, pager, next"
                        :current-page.sync="page"
                        :page-size.sync="pageSize"
                        :total="total"
                >
                </el-pagination>
            </el-tab-pane>
            <!--二-->
            <el-tab-pane label="分类添加" name="second">
                <el-col :span="12">
                    <el-form label-width="80px" :model="form" :rules="rules" ref="submitForm">
                        <el-form-item label="分类名称" prop="name">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-form-item label="分类图片" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    action="/api/upload/upload"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="form.thumb" :src="form.thumb" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="分类排序" prop="sort">
                            <el-input v-model="form.sort"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submit">立即新增</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-tab-pane>

        </el-tabs>

    </el-card>
    </div>
</template>

<script>
    import Header from "../../components/Header";
    export default {

        data() {
            return {
                api:"/api/type/type",
                activeName:"first",
                tableData:[],
                page:1,
                pageSize:5,
                total:0,
                form:{
                    name:"",
                    thumb:"",
                    sort:""
                },
                rules:{
                    name:{required: true,message: "请输入分类名称",trigger: "blur"},
                    thumb: {required: true, message: '请输入分类图片', trigger: "blur"},
                    sort: {required: true, message: '请输入分类排序', trigger: "blur"},
                }
            }
        },
        methods: {
            fetchData(){
                // 发送请求
                this.axios.get("/api/type/type",{
                    params:{
                        page:this.page,
                        pageSize:this.pageSize,
                    },
                }).then(res=>{

                        this.tableData=res.data.data;
                        this.total=res.data.total;

                })
            },
            //图片上传
            handleAvatarSuccess(res, file) {
                this.form.thumb = res;
                this.$refs.submitForm.validate("thumb");

            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            //提交表单
            submit(){
                this.$refs.submitForm.validate(valid=>{
                    if (valid){
                        this.axios.post(this.api,this.form).then(res=>{
                            if (res.data.code===200){
                                this.$message.success("提交成功");
                                this.$refs.submitForm.resetFields();
                                this.activeName="first";
                                this.fetchData();
                            }
                        })
                    }else{
                        this.$message.error("验证失败")
                    }
                })
            },

            del(id){
                this.$confirm('此操作将永久删除分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete("/api/type/type",{
                        params:{id}
                    }).then(res=>{
                        if(res.data.code===200){
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            edit(obj){
                this.updateForm=obj;
                //显示
                this.dialogVisible=true;
            },
            update(){
                delete this.updateForm.create_time;
                this.axios.put("/api/type/type",this.updateForm).then(res=>{
                    if(res.data.code===200){
                        this.dialogVisible=false;
                        this.$message.success("修改成功");
                        this.fetchData();
                    }
                })

            }
        },

        change(){
            this.fetchData()
        },
        //监听
        watch:{
            page:function () {
                this.fetchData();
            }
        },
        //局部注册
        components:{
          "basic-header":Header
        },
        created() {
            this.fetchData()
        },
        mounted() {

        }
    }
</script>

<style scoped>
    .avatar-uploader .el-upload {

        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>